.profile__container-avatar{
    grid-area: avatar;
  width: 120px;
  height: 120px;
  border-radius: 60px;
  position: relative;
}

.profile__container-avatar:hover {
    cursor: pointer;
  }
  
  .profile__container-avatar:hover::before {
    content: url(../../../images/Vector.svg);
    background-repeat: no-repeat;
    max-width: 26px;
    max-height: 26px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 47px;
    left: 46px;
  }