.popup__container_type_info-tooltip {
    height: 330px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 32px;
    text-align: center;
  }
  
  @media screen and (max-width: 696px) {
    .popup__container_type_info-tooltip {
      width: 280px;
      height: 308px;
      row-gap: 40px;
    }
  
    .popup__container_type_info-tooltip > .popup__title {
      font-size: 20px;
      line-height: calc(24 / 20 * 100%);
    }
  }