.welcome-window__heading {
    margin: 0;
    max-width: 358px;
    font-size: 24px;
    font-weight: 900;
    line-height: calc(29 / 24 * 100%);
  }

  @media screen and (max-width: 696px) {
    .welcome-window__heading {
      max-width: calc(260px + (98 + 98 * .7) * (100vw - 320px) / 696);
      font-size: calc(20px + (4 + 4 * .7) * (100vw - 320px) / 696);
      line-height: calc(24 / 20 * 100%);
    }
  }